h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

@font-face {
  font-family: Proxima Nova;
  src: url("ProximaNova-Bold.1143aebc.ttf") format("ttf");
  font-weight: 700;
}

@font-face {
  font-family: Proxima Nova;
  src: url("ProximaNova-Regular.3c2fd87b.ttf") format("ttf");
  font-weight: 400;
}

html {
  scroll-behavior: smooth;
}

body {
  color: #000;
  background-color: #fff;
  font-family: Proxima Nova, sans-serif;
}

body.modal-open {
  overflow: hidden;
}

.visualy-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.section {
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 480px) {
  .section {
    padding-left: 0;
    padding-right: 0;
  }
}

.section__title {
  text-align: center;
  color: #1e2e36;
  margin-top: 50px;
  margin-bottom: 25px;
  font-family: Proxima Nova, sans-serif;
  font-size: 36px;
  font-weight: 700;
  line-height: 125%;
}

.section__title:before {
  content: "";
  width: 12px;
  height: 35px;
  z-index: -1;
  background-color: #44c697;
  margin-top: 5px;
  margin-left: -1px;
  position: absolute;
}

@media screen and (min-width: 768px) {
  .section__title {
    margin-top: 80px;
    margin-bottom: 40px;
    font-size: 40px;
  }

  .section__title:before {
    width: 15px;
    height: 40px;
    margin-top: 4px;
    margin-left: -2px;
  }
}

@media screen and (min-width: 1280px) {
  .section__title {
    margin-top: 100px;
    margin-bottom: 50px;
    font-size: 48px;
  }

  .section__title:before {
    width: 20px;
    height: 50px;
    margin-top: 5px;
    margin-left: -4px;
  }
}

