.novetly {
  width: 280px;
  height: 350px;
  background-color: #fff;
  border-left: 15px solid #44c697;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  left: 15px;
}

@media screen and (min-width: 768px) {
  .novetly {
    width: 400px;
    height: 500px;
    left: 40px;
  }
}

@media screen and (min-width: 1280px) {
  .novetly {
    width: 462px;
    height: 670px;
    left: 51px;
  }
}

.wave-text {
  position: absolute;
  top: 5%;
  left: 50%;
  transform: translate(-50%);
}

@media screen and (min-width: 768px) {
  .wave-text {
    top: 8%;
  }
}

@media screen and (min-width: 1280px) {
  .wave-text {
    top: 10%;
  }
}

.wave-text span {
  position: absolute;
  top: 0;
  left: 0;
}

.wave-text h2 {
  -webkit-text-stroke: 2px #44c697;
  color: rgba(0, 0, 0, 0);
  font-family: Proxima Nova, sans-serif;
  font-size: 40px;
}

@media screen and (min-width: 768px) {
  .wave-text h2 {
    font-size: 50px;
  }
}

@media screen and (min-width: 1280px) {
  .wave-text h2 {
    font-size: 60px;
  }
}

.wave-text span {
  color: #44c697;
  font-size: 40px;
  font-weight: bold;
  animation: wave 3s ease-in-out infinite;
}

@media screen and (min-width: 768px) {
  .wave-text span {
    font-size: 50px;
  }
}

@media screen and (min-width: 1280px) {
  .wave-text span {
    font-size: 60px;
  }
}

@keyframes wave {
  0% {
    -webkit-clip-path: polygon(0% 45%, 15% 44%, 32% 50%, 53% 61%, 70% 58%, 64% 59%, 100% 52%, 100% 100%, 0% 100%);
    clip-path: polygon(0% 45%, 15% 44%, 32% 50%, 53% 61%, 70% 58%, 64% 59%, 100% 52%, 100% 100%, 0% 100%);
  }

  50% {
    -webkit-clip-path: polygon(0% 60%, 16% 65%, 34% 66%, 51% 62%, 67% 50%, 84% 45%, 100% 46%, 100% 100%, 0% 100%);
    clip-path: polygon(0% 60%, 16% 65%, 34% 66%, 51% 62%, 67% 50%, 84% 45%, 100% 46%, 100% 100%, 0% 100%);
  }

  100% {
    -webkit-clip-path: polygon(0% 45%, 15% 44%, 32% 50%, 53% 61%, 70% 58%, 64% 59%, 100% 52%, 100% 100%, 0% 100%);
    clip-path: polygon(0% 45%, 15% 44%, 32% 50%, 53% 61%, 70% 58%, 64% 59%, 100% 52%, 100% 100%, 0% 100%);
  }
}

